import React, { useState, useEffect } from 'react';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import PerfectScrollbar from 'react-perfect-scrollbar';
import {
  Box,
  Card,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Typography,
  makeStyles,
  Button
} from '@material-ui/core';
import getInitials from '../../../utility/getInitials';
import ScheduleDilougForm from './ScheduleDilougForm';
import {addSchedule, deleteSchedule} from '../../../services/api';

const useStyles = makeStyles((theme) => ({
  root: {},
  avatar: {
    marginRight: theme.spacing(2)
  }
}));


const Results = ({ className, date, employees, ...rest }) => {
  const classes = useStyles();

  // console.log(employees);
  const monday = date;
  const tuesday = date + 1 * 24 * 60 * 60 * 1000;
  const wednesday = date + 2 * 24 * 60 * 60 * 1000;
  const thursday = date + 3 * 24 * 60 * 60 * 1000;
  const friday = date + 4 * 24 * 60 * 60 * 1000;
  const satday = date + 5 * 24 * 60 * 60 * 1000;
  const sunday = date + 6 * 24 * 60 * 60 * 1000;

  console.log(employees);

  function displayDate(date){
    return new Date(date).toString().slice(0,10);
  } 

  //
  const getTimeEntries = (entries) => {
    addSchedule(entries).then((response) => {
      if(response.status === 'ok') {
        console.log(response);
      } else {
        // Failed
        console.log("Failed at time entries");
      };
    })
  }

  const deleteTimeEntries = (entries) => {
    deleteSchedule(entries).then((response) => {
      if(response.status === 'ok') {
        console.log(response);
      } else {
        // Failed
        console.log("Failed to delete time entries");
      };
    })
  }


  return (
    <Card
      className={clsx(classes.root, className)}
      {...rest}
    >
      <PerfectScrollbar>
        <Box minWidth={1050}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>
                  Name
                </TableCell>
                <TableCell>
                  {displayDate(monday)}
                </TableCell>
                <TableCell>
                  {displayDate(tuesday)}
                </TableCell>
                <TableCell>
                  {displayDate(wednesday)}
                </TableCell>
                <TableCell>
                  {displayDate(thursday)}
                </TableCell>
                <TableCell>
                  {displayDate(friday)}
                </TableCell>
                <TableCell>
                  {displayDate(satday)}
                </TableCell>
                <TableCell>
                  {displayDate(sunday)}
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {employees.map((employee) => (
                <TableRow
                  hover
                  key={employee.id}
                >
                  <TableCell>
                    <Box
                      alignItems="center"
                      display="flex"
                    >
                      <Typography
                        color="textPrimary"
                        variant="body1"
                      >
                        {employee.firstName +" "+ employee.lastName}
                      </Typography>
                    </Box>
                  </TableCell>
                  <TableCell>
                    <ScheduleDilougForm id={employee.id} name={employee.firstName + ' '+ employee.lastName} startdate={monday} date={monday} day={'monday'} starttime={employee.week[0].starttime} endtime={employee.week[0].endtime}  onAdd={getTimeEntries} onDelete={deleteTimeEntries}/>
                  </TableCell>
                  <TableCell>
                  <ScheduleDilougForm id={employee.id} name={employee.firstName + ' '+ employee.lastName} startdate={monday} date={tuesday} day={'tuesday'} starttime={employee.week[1].starttime} endtime={employee.week[1].endtime} onAdd={getTimeEntries} onDelete={deleteTimeEntries}/>
                  </TableCell>
                  <TableCell>
                  <ScheduleDilougForm id={employee.id} name={employee.firstName + ' '+ employee.lastName} startdate={monday} date={wednesday} day={'wednesday'} starttime={employee.week[2].starttime} endtime={employee.week[2].endtime} onAdd={getTimeEntries} onDelete={deleteTimeEntries}/>
                  </TableCell>
                  <TableCell>
                  <ScheduleDilougForm id={employee.id} name={employee.firstName + ' '+ employee.lastName} startdate={monday} date={thursday} day={'thursday'} starttime={employee.week[3].starttime} endtime={employee.week[3].endtime} onAdd={getTimeEntries} onDelete={deleteTimeEntries}/>
                  </TableCell>
                  <TableCell>
                  <ScheduleDilougForm id={employee.id} name={employee.firstName + ' '+ employee.lastName} startdate={monday} date={friday} day={'friday'} starttime={employee.week[4].starttime} endtime={employee.week[4].endtime} onAdd={getTimeEntries} onDelete={deleteTimeEntries}/>
                  </TableCell>
                  <TableCell>
                  <ScheduleDilougForm id={employee.id} name={employee.firstName + ' '+ employee.lastName} startdate={monday} date={satday} day={'saturday'} starttime={employee.week[5].starttime} endtime={employee.week[5].endtime} onAdd={getTimeEntries} onDelete={deleteTimeEntries}/>
                  </TableCell>
                  <TableCell>
                  <ScheduleDilougForm id={employee.id} name={employee.firstName + ' '+ employee.lastName} startdate={monday} date={sunday} day={'sunday'} starttime={employee.week[6].starttime} endtime={employee.week[6].endtime} onAdd={getTimeEntries} onDelete={deleteTimeEntries}/>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </Box>
      </PerfectScrollbar>
    </Card>
  );
};

Results.propTypes = {
  className: PropTypes.string,
  employees: PropTypes.array.isRequired
};

export default Results;
