import React, { useState, useEffect } from 'react';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import {
  Box,
  Button,
  Card,
  CardContent,
  CardHeader,
  Divider,
  Grid,
  TextField,
  makeStyles
} from '@material-ui/core';
import { getEmployee, updateEmployee } from '../../../services/api';
import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert from '@material-ui/lab/Alert';
import {
  validateEmail,
  validateFirstName,
  validateLastName,
  validatePhone,
  validateString
} from '../../../utility/validation'
import { useNavigate } from 'react-router-dom';

const useStyles = makeStyles(() => ({
  root: {}
}));

function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const EmployeeDetails = ({ className, props }) => {
  const classes = useStyles();
  const navigate = useNavigate();
  const [firstName, setFirstname] = useState();
  const [lastName, setLastname] = useState();
  const [email, setEmail] = useState();
  const [phone, setPhone] = useState();
  const [role, setRole] = useState();
  const [street, setStreet] = useState();
  const [city, setCity] = useState();
  const [province, setProvince] = useState();
  const [country, setCountry] = useState();
  const [zipcode, setZipcode] = useState();
  const [open, setOpen] = useState(false);
  const [message, setMessage] = useState();

  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }
    setOpen(false);
  };

  useEffect(() => {
    getEmployee().then((res) => {
      setFirstname(res.data.firstName)
      setLastname(res.data.lastName)
      setEmail(res.data.username)
      setRole(res.data.role)
      if(res.data.userdetails) {
        setPhone(res.data.userdetails.phone)
        setStreet(res.data.userdetails.street)
        setCity(res.data.userdetails.city)
        setProvince(res.data.userdetails.province)
        setCountry(res.data.userdetails.country)
        setZipcode(res.data.userdetails.zipcode)
      }
    })
  }, [])

  const onSubmitForm = (event) => {
    event.preventDefault();
    let data ={}
    if(validateFirstName(firstName)) {
      data.firstName = firstName
    }  else {
      data.firstName = undefined;
    };
    if(validateLastName(lastName)) {
      data.lastName = lastName
    }  else {
      data.lastName = undefined;
    };
    if(validatePhone(phone)) {
      data.phone = phone
    }  else {
      data.phone = undefined;
    };
    if(validateString(street)) {
      data.street = street
    }  else {
      data.street = undefined;
    };
    if(validateString(city)) {
      data.city = city
    }  else {
      data.city = undefined;
    };
    if(validateString(province)) {
      data.province = province
    }  else {
      data.province = undefined;
    };
    if(validateString(country)) {
      data.country = country
    }  else {
      data.country = undefined;
    };
    
    data.role = role;
    data.zipcode = zipcode;
    /*let data = {
      firstName : firstName,
      lastName : lastName,
      phone: phone,
      role : role,
      street: street,
      city: city,
      province: province,
      country: country,
      zipcode: zipcode
    };*/
    console.log(data)
    const result = Object.values(data).filter((element) => element === undefined || element === "" || element === '')
    const userrole = localStorage.getItem("userrole")
    if(result.length > 0) {
      setOpen(true);
      setMessage("Please Verify all details")
    } else {
      setOpen(false);
      updateEmployee(data).then((res)=> {
        if(res.status === "ok") {
          if(userrole === "Manager") {
            navigate('/app/dashboard', { replace: true });
          } else {
            navigate('/app/attendence', { replace: true });
          }
        } else {
          setOpen(true);
          setMessage("Please try again")
        }          
      });
    }
  }

  return (
    <form
      noValidate
      onSubmit={onSubmitForm}
      autoComplete="off"
      className={clsx(classes.root, className)}
    >
      <Card>
      <Snackbar open={open} autoHideDuration={6000} onClose={handleClose}>
        <Alert onClose={handleClose} severity="error">
          {message}
        </Alert>
      </Snackbar>
        <CardHeader
          subheader="The information can be edited"
          title="Employee details"
        />
        <Divider />
        <CardContent>
          <Grid
            container
            spacing={3}
          >
            <Grid
              item
              md={6}
              xs={12}
            >
              <TextField
              variant="outlined"
              helperText="Please specify the first name"
              required
              fullWidth
              id="firstName"
              placeholder="First Name"
              name="firstName"
              onChange={event => setFirstname(event.target.value)}
              value={firstName || undefined}
            />
            </Grid>
            <Grid
              item
              md={6}
              xs={12}
            >
              <TextField
              variant="outlined"
              helperText="Please specify the last name"
              required
              fullWidth
              name="lastName"
              placeholder="Last Name"
              id="lastName"              
              onChange={event => setLastname(event.target.value)}
              value={lastName || ''}
            />
            </Grid>
            <Grid
              item
              md={6}
              xs={12}
            >
              <TextField
              variant="outlined"
              helperText="Please specify the Email"
              required
              fullWidth
              name="username"
              placeholder="Email"
              id="username"
              value={email}
              readonly
            />
            </Grid>
            <Grid
              item
              md={6}
              xs={12}
            >
              <TextField
                fullWidth
                helperText="Please specify the phone number"
                placeholder="Phone Number"
                name="phone"
                type="number"
                variant="outlined"
                required
                onChange={event => setPhone(event.target.value)}
                value={phone || ''}
              />
            </Grid>
            <Grid
              item
              md={6}
              xs={12}
            >
              <TextField
                fullWidth
                name="role"
                required
                SelectProps={{ native: true }}
                variant="outlined"
                value={role}
                helperText="Your Role is specified here"
                readonly/>
            </Grid>
            <Grid
              item
              md={6}
              xs={12}
            >
              <TextField
                fullWidth
                helperText="Please specify the street"
                placeholder="Street"
                name="streetname"
                type="text"
                variant="outlined"
                required
                onChange={event => setStreet(event.target.value)}
                value={street || ''}
              />
            </Grid>
            <Grid
              item
              md={6}
              xs={12}
            >
              <TextField
                fullWidth
                helperText="Please specify the City"
                placeholder="City"
                name="city"
                type="text"
                variant="outlined"
                required
                onChange={event => setCity(event.target.value)}
                value={city || ''}
              />
            </Grid>
            <Grid
              item
              md={6}
              xs={12}
            >
              <TextField
                fullWidth
                helperText="Please specify the Province"
                placeholder="Province"
                name="province"
                type="text"
                variant="outlined"
                required
                onChange={event => setProvince(event.target.value)}
                value={province || ''}
              />
            </Grid>
            <Grid
              item
              md={6}
              xs={12}
            >
              <TextField
                fullWidth
                helperText="Please specify the Country"
                placeholder="Country"
                name="country"
                type="text"
                variant="outlined"
                required
                onChange={event => setCountry(event.target.value)}
                value={country || ''}
              />
            </Grid>
            <Grid
              item
              md={6}
              xs={12}
            >
              <TextField
                fullWidth
                helperText="Please specify the Zipcode"
                placeholder="Zipcode"
                name="zipcode"
                type="text"
                variant="outlined"
                required
                onChange={event => setZipcode(event.target.value)}
                value={zipcode || ''}
              />
            </Grid>
          </Grid>
        </CardContent>
        <Divider />
        <Box
          display="flex"
          justifyContent="flex-end"
          p={2}
        >
          <Button
            color="primary"
            variant="contained"
            type="submit"
          >
            Save details
          </Button>
        </Box>
      </Card>
    </form>
  );
};

EmployeeDetails.propTypes = {
  className: PropTypes.string
};

export default EmployeeDetails;
