import React from "react";
// nodejs library that concatenates classes
import classNames from "classnames";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";

// @material-ui/icons

// core components
import Headerlanding from "../../componenets/Header-landingpage/Header";
import Footer from "../../componenets/Footer/Footer";
import GridContainer from "../../componenets/Grid/GridContainer";
import GridItem from "../../componenets/Grid/GridItem";
import Button from "../../componenets/CustomButtons/Button";
import HeaderLinks from "../../componenets/Header-landingpage/HeaderLinks";
import Parallax from "../../componenets/Parallax/Parallax";

import styles from "../../assets/jss/material-kit-react/views/landingPage.js";
import bgimag from "../../assets/img/landing-bg.jpg";
import { Link } from "react-router-dom";

// Sections for this page
import ProductSection from "./Sections/ProductSection.js";
import TeamSection from "./Sections/TeamSection.js";
import WorkSection from "./Sections/WorkSection.js";

const dashboardRoutes = [];

const useStyles = makeStyles(styles);

export default function LandingPage(props) {
  const classes = useStyles();
  const { ...rest } = props;
  return (
    <div>
      <Headerlanding
        color="white"
        routes={dashboardRoutes}
        brand="Time Keeper"
        rightLinks={<HeaderLinks />}
        fixed
        changeColorOnScroll={{
          height: 400,
          color: "white"
        }}
        {...rest}
      />
      <Parallax filter image={bgimag}>
        <div className={classes.container}>
          <GridContainer>
            <GridItem xs={12} sm={12} md={6}>
              <h1 className={classes.title}>Your Employee Management Starts With Us.</h1>
              <h4>
                Employee management is very easy using the TIMEKEEPER. 
                A great experience using this plateform 

              </h4>
              <br />
              <Button
                color="danger"
                size="lg"
                rel="noopener noreferrer"
                
              >
                <Link to="/register" style={{color: "white"}}>
                <i className="fas fa-play" />
                Register Now
              </Link></Button>
            </GridItem>
          </GridContainer>
        </div>
      </Parallax>
      <div className={classNames(classes.main, classes.mainRaised)} style={{"font-family": "Arial, Helvetica, sans-serif"}}>
        <div className={classes.container}>
          <ProductSection />
          <TeamSection />
          <WorkSection />
        </div>
      </div>
      <Footer />
    </div>
  );
}
