import React, { useState, useEffect } from 'react';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import {
  Box,
  Button,
  Card,
  CardContent,
  CardHeader,
  Divider,
  Grid,
  TextField,
  makeStyles
} from '@material-ui/core';
import { applyLeave } from '../../../services/api';
import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert from '@material-ui/lab/Alert';
import { Link as RouterLink, useNavigate } from 'react-router-dom';

const useStyles = makeStyles(() => ({
  root: {},
  textarea: {
    resize: "both"
  }
}));

function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const LeaveForm = ({ className, props }) => {
  const classes = useStyles();
  const navigate = useNavigate();

  const [fromDate, setFromDate] = useState();
  const [toDate, setToDate] = useState();
  const [reason, setReason] = useState();
  const [open, setOpen] = useState(false);
  const [message, setMessage] = useState();

  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }
    setOpen(false);
  };

  const onSubmitForm = (event) => {
    event.preventDefault();
    let data = {
      fromDate,
      toDate,
      reason
    };
    console.log(data)
    const result = Object.values(data).filter((element) => element === undefined || element === "" || element === '')
    if(result.length > 0) {
      setOpen(true);
      setMessage("Please Verify all details")
    } else {
      setOpen(false);
      applyLeave(data).then((res) => {
        if(res.status === 'ok')
          navigate('/app/get/leave', { replace: true });
      })
    }
  }

  return (
    <form
      noValidate
      onSubmit={onSubmitForm}
      autoComplete="off"
      className={clsx(classes.root, className)}
    >
      <Card>
      <Snackbar open={open} autoHideDuration={6000} onClose={handleClose}>
        <Alert onClose={handleClose} severity="error">
          {message}
        </Alert>
      </Snackbar>
        <CardHeader
          subheader="The information cannot be edited"
          title="Leave Application"
        />
        <Divider />
        <CardContent>
          <Grid
            container
            spacing={3}
          >
            <Grid
              item
              md={6}
              xs={12}
            >
              <TextField
                id="datetime-from"
                label="From"
                type="datetime-local"
                defaultValue={new Date().toISOString().substring(0, 10)}
                className={classes.textField}
                InputLabelProps={{
                  shrink: true,
                }}
                name="fromDate"
                onChange={event => setFromDate(event.target.value)}
              />
            </Grid>
            <Grid
              item
              md={6}
              xs={12}
            >
              <TextField
                id="datetime-to"
                label="To"
                type="datetime-local"
                defaultValue={new Date().toISOString().substring(0, 10)}
                className={classes.textField}
                InputLabelProps={{
                  shrink: true,
                }}
                name="toDate"
                onChange={event => setToDate(event.target.value)}
              />
            </Grid>
            <Grid
              item
              md={6}
              xs={12}
            >
              <TextField
                id="leave-reason"
                label="Leave Reason"
                placeholder="Leave Reason"
                multiline
                variant="outlined"
                inputProps={{ className: classes.textarea }}
                name="reason"
                onChange={event => setReason(event.target.value)}
              />
            </Grid>
          </Grid>
        </CardContent>
        <Divider />
        <Box
          display="flex"
          justifyContent="flex-end"
          p={2}
        >
          <Button
            color="primary"
            variant="contained"
            type="submit"
          >
            Apply Leave
          </Button>
        </Box>
      </Card>
    </form>
  );
};

LeaveForm.propTypes = {
  className: PropTypes.string
};

export default LeaveForm;
