import React, { useState } from 'react';
import { Link as RouterLink, useNavigate } from 'react-router-dom';
import * as Yup from 'yup';
import { Formik } from 'formik';
import {
  Box,
  Button,
  Container,
  TextField,
  Card,
  CardContent,
  CardHeader,
  Divider,
  Grid,
  makeStyles,
  Typography
} from '@material-ui/core';
import Page from '../../componenets/Page';
import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert from '@material-ui/lab/Alert';
import { validateEmail } from '../../utility/validation'
import { getCode } from '../../services/api';

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    height: '100%',
    paddingBottom: theme.spacing(3),
    paddingTop: theme.spacing(3)
  }
}));

function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const ForgetPasswordView = () => {
  const classes = useStyles();
  const navigate = useNavigate();

  const [email, setEmail] = useState();
  const [open, setOpen] = useState(false);
  const [message, setMessage] = useState();

  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }
    setOpen(false);
  };

  const handleEmail = (event) => {
    if(!validateEmail(event.target.value)) {
      setOpen(true);
      setMessage("Please Enter Valid E-Mail")
    } else {
      setOpen(false);
      setEmail(event.target.value)
    }
  }

  const onSubmitForm = (event) => {
    event.preventDefault();
    let data = {
        email
    }

    const result = Object.values(data).filter((element) => element === undefined)
    if(result.length > 0) {
      setOpen(true);
      setMessage("Please Verify all details")
    } else {
      setOpen(false);
      getCode(data).then((response)=>{
        if(response.status === "ok") {
            localStorage.setItem("username", response.data.username);
            navigate('/verify-code', { replace: true });
        } else {
            setOpen(true);
            setMessage("Please Try Again!!")
        }
      });
    }
  }

  return (
    <Page
      className={classes.root}
      title="Forget Password"
    >
      <Box
        display="flex"
        flexDirection="column"
        height="100%"
        justifyContent="center"
      >
        <Container maxWidth="sm">
          <Formik
            initialValues={{
              code: ''
            }}
          >
            {({
              errors,
              handleBlur,
              handleChange,
              handleSubmit,
              isSubmitting,
              touched,
              values
            }) => (
              <form noValidate onSubmit={onSubmitForm} autoComplete="off">
                <Box mb={3}>
                  <Typography
                    color="textPrimary"
                    variant="h2"
                  >
                    Forgot Password
                  </Typography>
                  <Typography
                    color="textSecondary"
                    gutterBottom
                    variant="body2"
                  >
                    Enter your registered email address
                  </Typography>
                </Box>
                <Box
                  mt={3}
                  mb={1}
                ></Box>
                  <TextField
                    fullWidth
                    label="Registered Email Address"
                    name="email"
                    onBlur={handleBlur}
                    onChange={event => handleEmail(event)}
                    variant="outlined"
                  />                
                <Box
                  my={2}
                >
                  <Button
                    fullWidth
                    size="large"
                    type="submit"
                    variant="outlined"
                    color="primary"
                  >
                    Reset Password
                  </Button>
                </Box>
              </form>
            )}
          </Formik>
        </Container>
      </Box>
    </Page>
  );
};

export default ForgetPasswordView
