import React, {useState} from 'react';
import { successDonation } from '../../services/api';

const DonationSuccess = () => {
    
    const success = (event) => {
        if(localStorage.getItem("userrole") == "Manager"){
            window.location = "/app/dashboard";
        }
        else{
            window.location = "/app/attendence";
        }
    }
    return(
        <div >
            <br/>
            <h1>Thank you for your donation today</h1>
            <button 
                style={{"background-color":"#6247aa", 
                border: "none",
                color: "white",
                padding: "15px 32px",
                "text-align": "center",
                "text-decoration": "none",
                display: "inline-block",
                "font-size": "16px",
                margin: "4px 2px",
                cursor: "pointer"}}
                onClick={success}>return
            </button>
        </div>
        
    )
}
export default DonationSuccess;