import React from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";

// @material-ui/icons
import ScheduleIcon from '@material-ui/icons/Schedule';
import PeopleAltIcon from '@material-ui/icons/PeopleAlt';
import AssignmentIcon from '@material-ui/icons/Assignment';
// core components
import GridContainer from "../../../componenets/Grid/GridContainer";
import GridItem from "../../../componenets/Grid/GridItem";
import InfoArea from "../../../componenets/InfoArea/InfoArea";

import styles from "../../../assets/jss/material-kit-react/views/landingPageSections/productStyle.js";

const useStyles = makeStyles(styles);

export default function ProductSection() {
  const classes = useStyles();
  return (
    <div className={classes.section}>
      <GridContainer justify="center">
        <GridItem xs={12} sm={12} md={8}>
          <h2 className={classes.title}>Let{"'"}s talk about product</h2>
          <h5 className={classes.description} style={{color:"#4D4D4D", "font-family": "Arial, Helvetica, sans-serif"}}>
            Time keeper has many features that helps in the employee management effectively 
            Attandence, EmployeeOnboard, Leave management, Donation these are the features that 
            will provide all in one plateform for the management with ease and accuratley.
          </h5>
        </GridItem>
      </GridContainer>
      <div>
        <GridContainer>
          <GridItem xs={12} sm={12} md={4}>
            <InfoArea
              title="Scheduling"
              description="Manage the everyday schedule using our software and maintain the data easily. Assign shift timing to all the available employees easily"
              icon={ScheduleIcon}
              iconColor="info"
              vertical
              
            />
          </GridItem>
          <GridItem xs={12} sm={12} md={4}>
            <InfoArea
              title="Emlpoyee Onboard"
              description="Manage all the employee data in organized manner. Easily onboard new empolyees and manage or update details"
              icon={PeopleAltIcon}
              iconColor="info"
              vertical
            />
          </GridItem>
          <GridItem xs={12} sm={12} md={4}>
            <InfoArea
              title="Leave Management"
              description="Employees can easily apply for the leaves through the portal and manager can approve or decline them. Easy to maintain the leave list"
              icon={AssignmentIcon}
              iconColor="info"
              vertical
            />
          </GridItem>
        </GridContainer>
      </div>
    </div>
  );
}
