import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { useNavigate } from 'react-router-dom';
import {
  makeStyles,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Button
} from '@material-ui/core';
import { getOrganizationLeaves, updateLeaves } from '../../../services/api'

const useStyles = makeStyles(() => ({
  root: {},
  table: {
    minWidth: 650,
  }
}));

const LeaveList = ({ className, props }) => {
  const classes = useStyles();
  const navigate = useNavigate();
  let [row, setRow] = useState([]);
  let rows = [];
  let userrole = '';

  function createData(name, from, to, reason, status, id) {
    return { name, from, to, reason, status, id};
  }

  useEffect(() => {
    userrole = localStorage.getItem("userrole");
    getOrganizationLeaves().then((res) => {
      const data = res.data;
      for(let leave of data) {
        rows.push(createData(leave.username, leave.fromDate, leave.toDate, leave.reason, leave.status, leave._id))
      }
      setRow(rows);
    });
  }, [])

  const leaveAction = (id, action) => {
    let data = {}
    if(action === "approve") {
      data = {
        status: "APPROVED",
        _id:id
      }
    } else {
      data = {
        status: "REJECTED",
        _id:id
      }
    }
    updateLeaves(data).then(res =>{
      // navigate('/app/approve/leave', { replace: true })
      window.location.reload();
    });
  } 

  return (
    <TableContainer component={Paper}>
      <Table className={classes.table} aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell>User Name</TableCell>
            <TableCell >From</TableCell>
            <TableCell >To</TableCell>
            <TableCell >Reason</TableCell>
            <TableCell >Status</TableCell>
            <TableCell >Action</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {row.map((row, index) => (
            <TableRow key={index}>
              <TableCell component="th" scope="row">
                {row.name}
              </TableCell>
              <TableCell >{row.from}</TableCell>
              <TableCell >{row.to}</TableCell>
              <TableCell >{row.reason}</TableCell>
              <TableCell >{row.status}</TableCell>
              {
                row.status === "APPLIED" ? <TableCell >
                <Button
                  color="primary"
                  variant="contained"
                  onClick={() => {
                    leaveAction(row.id, "approve")
                  }}    
                >
                  Approve
                </Button>
                <span> </span>
                <Button
                  color="primary"
                  variant="outlined"
                  onClick={() => {
                    leaveAction(row.id, "reject")
                  }}    
                >
                  Reject
                </Button>
                </TableCell> : <TableCell></TableCell>
              }
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
};

LeaveList.propTypes = {
  className: PropTypes.string
};

export default LeaveList;
