import React,{useState} from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";

// @material-ui/icons
import {
  TextField
} from '@material-ui/core';
// core components
import GridContainer from "../../../componenets/Grid/GridContainer";
import GridItem from "../../../componenets/Grid/GridItem";
import CustomInput from "../../../componenets/CustomInput/CustomInput";
import Button from "../../../componenets/CustomButtons/Button";
import styles from "../../../assets/jss/material-kit-react/views/landingPageSections/workStyle.js";
import { validateEmail, validateString } from '../../../utility/validation';
import {createContact} from "../../../services/api";

const useStyles = makeStyles(styles);

export default function WorkSection() {
  const classes = useStyles();

  const [open, setOpen] = useState(false);
  const [msg, setMessage] = useState();
  const [name, setName] = useState();
  const [emailId, setEmail] = useState();
  const [message, setMsg] = useState();
  var blacklisted_words = require('../../../assets/blacklisted_words');
  
  const handleName = (event) => {
      if(!validateString(event.target.value)) {
        setOpen(true);
        setMessage("Please Enter Valid Name")
      } else {
        setOpen(false);
        setName(event.target.value)
      }
  }
  const handleEmail = (event) => {
      if(!validateEmail(event.target.value)) {
        setOpen(true);
        setMessage("Please Enter Valid Email Address")
      } else {
        setOpen(false);
        setEmail(event.target.value)
      }
  }
  const handleMessage = (event) => {
      if(!validateString(event.target.value)) {
        setOpen(true);
        setMessage("Please Enter Valid message")
      } else {
        setOpen(false);
        setMsg(event.target.value)
      }
  }
  
  const onSubmitForm = (event) => {
    event.preventDefault();
    
    let data = {
      name,
      emailId,
      message
    };
    let blackListWords =["abc", "def"];
    
    const result = Object.values(data).filter((element) => element === undefined)
    if(result.length > 0) {
      setOpen(true);
      setMessage("Please Verify all details")
    } else {
      setOpen(false);
      let msg = data.message;
    var msgArray = msg.split(" ");
    
    const found = msgArray.some(r=> blacklisted_words.includes(r));
    if(! found){
      createContact(data).then((response)=>{
        if(response.status === "ok") {
          console.log("contact saved");
          window.alert("Thanks for contacting Us");
        } else {
          setOpen(true);
          setMessage("Please Try Again!!")
        }
      });
    }
    else{
      window.alert("Sorry Can not send message!!! \nYour message contains blacklisted words");
    }
      
  }
  
    
}

  return (
    <div className={classes.section}>
      <GridContainer justify="center">
        <GridItem cs={12} sm={12} md={8}>
          <h2 className={classes.title}>Work with us</h2>
          <h4 className={classes.description} style={{color:"#4D4D4D", styles:""}}>
            Divide details about your product or agency work into parts. Write a
            few lines about each one and contact us about any further
            collaboration. We will responde get back to you in a couple of
            hours.
          </h4>
          <form onSubmit={onSubmitForm}>
            <GridContainer>
            <GridItem xs={5} sm={5} md={12}><br></br></GridItem>
              <GridItem xs={12} sm={12} md={6}>
                <TextField
                  labelText="Your Name"
                  label="Your Name"
                  id="name"
                  fullWidth
                  variant="outlined"
                  onChange={event => handleName(event)}
                />
              </GridItem>
              <GridItem xs={12} sm={12} md={6}>
                <TextField
                  labelText="Your Email"
                  label="Email Address"
                  id="email"
                  fullWidth
                  variant="outlined"
                  onChange={event => handleEmail(event)}
                />
              </GridItem>
              <GridItem xs={5} sm={5} md={12}><br></br></GridItem>
              <GridItem xs={5} sm={5} md={12}>
              <TextField
                labelText="Your Message"
                label="Your Message"
                id="message"
                fullWidth
                variant="outlined"
                inputProps={{
                  multiline: true,
                  rows: 5
                }}
                onChange={event => handleMessage(event)}
              />
              </GridItem>
              <GridItem xs={5} sm={5} md={12}><br></br></GridItem>
              <GridItem xs={12} sm={12} md={4}>
                <Button color="primary" type="submit">Send Message</Button>
              </GridItem>
            </GridContainer>
          </form>
        </GridItem>
      </GridContainer>
    </div>
  );
}
