import React, { useState } from 'react';
import { Link as RouterLink, useNavigate } from 'react-router-dom';
import * as Yup from 'yup';
import { Formik } from 'formik';
import {
  Box,
  Button,
  Container,
  Link,
  TextField,
  Typography,
  makeStyles,
  Grid
} from '@material-ui/core';
import Page from '../../componenets/Page';
import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert from '@material-ui/lab/Alert';
import { validatePassword, matchPassword, validateEmail } from '../../utility/validation'
import { changePasswrod } from '../../services/api';

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    height: '100%',
    paddingBottom: theme.spacing(3),
    paddingTop: theme.spacing(3)
  }
}));

function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const ChangePasswordView = () => {
  const classes = useStyles();
  const navigate = useNavigate();
  const [email] = useState(localStorage.getItem("username"));
  const [password, setPassword] = useState();
  const [confirmPassword, setConfirmPassword] = useState();
  const [open, setOpen] = useState(false);
  const [message, setMessage] = useState();

  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }
    setOpen(false);
  };

  const handleEmail = (event) => {
    if(!validateEmail(event.target.value)) {
      setOpen(true);
      setMessage("Please Enter Valid Email")
    } else {
      setOpen(false);
      setPassword(event.target.value)
    }
  }

  const handlePassword = (event) => {
    if(!validatePassword(event.target.value)) {
      setOpen(true);
      setMessage("Please Enter Valid Password")
    } else {
      setOpen(false);
      setPassword(event.target.value)
    }
  }

  const handleConfirmPassword = (event) => {
    if(!validatePassword(event.target.value) && !matchPassword(password, event.target.value)) {
      setOpen(true);
      setMessage("Please Enter Valid Password")
    } else {
      setOpen(false);
      setConfirmPassword(event.target.value)
    }
  }

  const onSubmitForm = (event) => {
    event.preventDefault();
    let data = {
      username: email.trim(),
      password: confirmPassword
    };

    const result = Object.values(data).filter((element) => element === undefined)
    if(result.length > 0) {
      setOpen(true);
      setMessage("Please Verify all details")
    } else {
      setOpen(false);
      changePasswrod(data).then((response)=>{
        if(response.status === "ok") {
          navigate('/login', { replace: true });
        } else {
          setOpen(true);
          setMessage("Please Try Again!!")
        }
      });
    }
  }

  return (
    <Page
      className={classes.root}
      title="Change Password"
    >
      <Box
        display="flex"
        flexDirection="column"
        height="100%"
        justifyContent="center"
      >
        <Container maxWidth="sm">
        <Snackbar open={open} autoHideDuration={6000} onClose={handleClose}>
          <Alert onClose={handleClose} severity="error">
            {message}
          </Alert>
        </Snackbar>
          <Formik
            initialValues={{
              email: '',
              organizationName: '',
              organizationWebsite: '',
              password: '',
              confirmPassword: '',
              policy: false
            }}
          >
            {({
              errors,
              handleBlur,
              touched
            }) => (
              <form noValidate onSubmit={onSubmitForm} autoComplete="off">
                <Box mb={3}>
                  <Typography
                    color="textPrimary"
                    variant="h2"
                  >
                    Change Password
                  </Typography>
                  <Typography
                    color="textSecondary"
                    gutterBottom
                    variant="body2"
                  >
                    Please enter new password
                  </Typography>
                </Box>
                <Box
                  mt={3}
                  mb={1}
                >
                </Box>
                  <Grid container spacing={1}>
                    <Grid item md={12} xs={12}>
                    <TextField
                      error={Boolean(touched.email && errors.email)}
                      fullWidth
                      helperText={touched.email && errors.email}
                      label="Email Address"
                      margin="normal"
                      name="email"
                      onBlur={handleBlur}
                      onChange={event => handleEmail(event)}
                      type="email"
                      variant="outlined"
                      value={email}
                      readOnly
                    />
                    </Grid>
                    <Grid item md={6} xs={12}>
                    <TextField
                      error={Boolean(touched.password && errors.password)}
                      fullWidth
                      helperText={touched.password && errors.password}
                      label="Password"
                      margin="normal"
                      name="password"
                      onBlur={handleBlur}
                      onChange={event => handlePassword(event)}
                      type="password"
                      variant="outlined"
                    />
                    </Grid>
                    <Grid item md={6} xs={12}>
                    <TextField
                      error={Boolean(touched.confirmPassword && errors.confirmPassword)}
                      fullWidth
                      helperText={touched.confirmPassword && errors.confirmPassword}
                      label="Confirm Password"
                      margin="normal"
                      name="confirmPassword"
                      onBlur={handleBlur}
                      onChange={event => handleConfirmPassword(event)}
                      type="password"
                      variant="outlined"
                    />
                    </Grid>
                  </Grid>
                <Box
                  my={2}
                >
                  <Button
                    color="primary"
                    fullWidth
                    size="large"
                    type="submit"
                    variant="contained"
                  >
                    Change Password
                  </Button>
                </Box>
                {/* <Typography
                  color="textSecondary"
                  variant="body1"
                >
                  Have an account?
                  {' '}
                  <Link
                    component={RouterLink}
                    to="/login"
                    variant="h6"
                  >
                    Sign in
                  </Link>
                </Typography> */}
              </form>
            )}
          </Formik>
        </Container>
      </Box>
    </Page>
  );
};

export default ChangePasswordView;
