import React from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import {
  Box,
  Button,
  makeStyles
} from '@material-ui/core';
import { Search as SearchIcon } from 'react-feather';

const useStyles = makeStyles((theme) => ({
  root: {},
  importButton: {
    marginRight: theme.spacing(1)
  },
  exportButton: {
    marginRight: theme.spacing(1)
  }
}));



const Toolbar = ({ date, onNextClick, onPrevClick, className, ...rest }) => {
  const classes = useStyles();

  const startDate = new Date(date);
  console.log("Date "+ date);
  console.log("StartDate "+startDate);
  const endDate = new Date(date + 6 * 24 * 60 * 60 * 1000);

  return (
    <div
      className={clsx(classes.root, className)}
      {...rest}
    >
      <Box
        display="flex"
        justifyContent="flex-end"
      >
        {/* <Button 
        variant="contained"
        className={classes.importButton}
        onClick={() => onPrevClick(date - 7*24*60*60*1000)}
        >
          Prev
        </Button> */}
        <Button
        variant="outlined"
        >
        {startDate.toDateString().slice(4,11)} - {endDate.toDateString().slice(4,11)}
        </Button>
        {/* <Button 
        variant="contained"
        className={classes.exportButton}
        onClick={() => onNextClick(date + 7*24*60*60*1000)}
        >
          Next
        </Button> */}
      </Box>
    </div>
  );
};

Toolbar.propTypes = {
  className: PropTypes.string
};

export default Toolbar;
