import React, {useState} from 'react';
import { Link as RouterLink, useNavigate } from 'react-router-dom';
import * as Yup from 'yup';
import { Formik } from 'formik';
import {
  Box,
  Button,
  Container,
  Link,
  TextField,
  Typography,
  makeStyles
} from '@material-ui/core';
import Page from '../../componenets/Page';
import { validateEmail, validatePassword } from '../../utility/validation';
import { login } from '../../services/api';
import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert from '@material-ui/lab/Alert';

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    height: '100%',
    paddingBottom: theme.spacing(3),
    paddingTop: theme.spacing(3)
  }
}));

function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const LoginView = () => {
    const classes = useStyles();
    const navigate = useNavigate();
  
    const [email, setEmail] = useState();
    const [password, setPassword] = useState();
    const [open, setOpen] = useState(false);
    const [message, setMessage] = useState();

    const handleClose = (event, reason) => {
      if (reason === 'clickaway') {
        return;
      }
      setOpen(false);
    };

    const handleEmail = (event) => {
      if(!validateEmail(event.target.value)) {
        setOpen(true);
        setMessage("Please Enter Valid Email Address")
      } else {
        setOpen(false);
        setEmail(event.target.value)
      }
    }

    const handlePassword = (event) => {
      if(!validatePassword(event.target.value)) {
        setOpen(true);
        setMessage("Please Enter Valid Password")
      } else {
        setOpen(false);
        setPassword(event.target.value)
      }
    }
  
    const onSubmitForm = (event) => {
        event.preventDefault();
        let data = {
          email,
          password
        };
  
        const result = Object.values(data).filter((element) => element === undefined)
        if(result.length > 0) {
          setOpen(true);
          setMessage("Please Verify all details")
        } else {
          login(data).then((response) => {
            if(response.status === "ok") {
              localStorage.setItem('token', response.token);
              localStorage.setItem('organizationId', response.user.organizationId);
              localStorage.setItem('userId', response.user._id);
              localStorage.setItem('userrole', response.user.role);
              if(response.user.role === "Manager") {
                navigate('/app/dashboard', { replace: true });
              } else {
                navigate('/app/attendence', { replace: true });
              }
              
            } else {
              setOpen(true);
              setMessage("Please try again!!")
            }
          });
        }
    }

  return (
    <Page
      className={classes.root}
      title="Login"
    >
      <Box
        display="flex"
        flexDirection="column"
        height="100%"
        justifyContent="center"
      >
        <Container maxWidth="sm">
          <Snackbar open={open} autoHideDuration={6000} onClose={handleClose}>
            <Alert onClose={handleClose} severity="error">
              {message}
            </Alert>
          </Snackbar>
          <Formik
            initialValues={{
              email: 'demo@devias.io',
              password: 'Password123'
            }}
            validationSchema={Yup.object().shape({
              email: Yup.string().email('Must be a valid email').max(255).required('Email is required'),
              password: Yup.string().max(255).required('Password is required')
            })}
            onSubmit={() => {
              navigate('/app/dashboard', { replace: true });
            }}
          >
            {({
              errors,
              handleBlur,
              isSubmitting,
              touched
            }) => (
              <form onSubmit={onSubmitForm}>
                <Box mb={3}>
                  <Typography
                    color="textPrimary"
                    variant="h2"
                  >
                    Sign in
                  </Typography>
                  <Typography
                    color="textSecondary"
                    gutterBottom
                    variant="body2"
                  >
                    Sign in with email address
                  </Typography>
                </Box>
                <Box
                  mt={3}
                  mb={1}
                >
                </Box>
                <TextField
                  error={Boolean(touched.email && errors.email)}
                  fullWidth
                  helperText={touched.email && errors.email}
                  label="Email Address"
                  margin="normal"
                  name="email"
                  onBlur={handleBlur}
                  type="email"
                  variant="outlined"
                  autoFocus
                  onChange={event => handleEmail(event)}
                />
                <TextField
                  error={Boolean(touched.password && errors.password)}
                  fullWidth
                  helperText={touched.password && errors.password}
                  label="Password"
                  margin="normal"
                  name="password"
                  onBlur={handleBlur}
                  type="password"
                  variant="outlined"
                  onChange={event => handlePassword(event)}
                />
                <Typography
                  color="textSecondary"
                  variant="body1"
                  align="right"
                >
                  Forgot password?
                  {' '}
                  <Link
                    component={RouterLink}
                    to="/forget-password"
                    variant="h6"
                  >
                    Click here
                  </Link>
                </Typography>
                <Box my={2}>
                  <Button
                    color="primary"
                    disabled={isSubmitting}
                    fullWidth
                    size="large"
                    type="submit"
                    variant="contained"
                  >
                    Sign in now
                  </Button>
                </Box>
                <Typography
                  color="textSecondary"
                  variant="body1"
                >
                  Don&apos;t have an account?
                  {' '}
                  <Link
                    component={RouterLink}
                    to="/register"
                    variant="h6"
                  >
                    Sign up
                  </Link>
                </Typography>
              </form>
            )}
          </Formik>
        </Container>
      </Box>
    </Page>
  );
};

export default LoginView;
