import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import {
  makeStyles,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper
} from '@material-ui/core';
import { getEmployee } from '../../../services/api'

const useStyles = makeStyles(() => ({
  root: {},
  table: {
    minWidth: 650,
  },
}));

function createDay(starttime, endtime){
  return {starttime, endtime}
}

const ScheduleList = ({ className, props }) => {
  const classes = useStyles();
  const [monday, setmonday] = useState(createDay('',''))
  const [tuesday, settuesday] = useState(createDay('',''))
  const [wednesday, setwednesday] = useState(createDay('',''))
  const [thursday, setthursday] = useState(createDay('',''))
  const [friday, setfriday] = useState(createDay('',''))
  const [saturday, setsaturday] = useState(createDay('',''))
  const [sunday, setsunday] = useState(createDay('',''))

  useEffect(() => {
    getEmployee().then((res) => {

      if(res.data.schedule) {
        for(let day of res.data.schedule.entries) {
          switch(day.day){
            case 'monday':
              setmonday(createDay(day.starttime, day.endtime));
              break;
            case 'tuesday':
              settuesday(createDay(day.starttime, day.endtime));
              break;
            case 'wednesday':
              setwednesday(createDay(day.starttime, day.endtime));
                break;
            case 'thursday':
              setthursday(createDay(day.starttime, day.endtime));
              break;
            case 'friday':
              setfriday(createDay(day.starttime, day.endtime));
              break;
            case 'saturday':
              setsaturday(createDay(day.starttime, day.endtime));
              break;
            case 'sunday':
              setsunday(createDay(day.starttime, day.endtime));
                break;                
            default:
          }

        }
      }
    })
  }, [])
  return (
    <TableContainer component={Paper}>
      <Table className={classes.table} aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell>Monday</TableCell>
            <TableCell >Tuesday</TableCell>
            <TableCell >Wednesday</TableCell>
            <TableCell >Thursday</TableCell>
            <TableCell >Friday</TableCell>
            <TableCell >Saturday</TableCell>
            <TableCell >Sunday</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
            <TableRow>
              <TableCell >{monday.starttime} - {monday.endtime}</TableCell>
              <TableCell >{tuesday.starttime} - {tuesday.endtime}</TableCell>
              <TableCell >{wednesday.starttime} - {wednesday.endtime}</TableCell>
              <TableCell >{thursday.starttime} - {thursday.endtime}</TableCell>
              <TableCell >{friday.starttime} - {friday.endtime}</TableCell>
              <TableCell >{saturday.starttime} - {saturday.endtime}</TableCell>
              <TableCell >{sunday.starttime} - {sunday.endtime}</TableCell>
            </TableRow>
        </TableBody>
      </Table>
    </TableContainer>
  );
};

ScheduleList.propTypes = {
  className: PropTypes.string
};

export default ScheduleList;
