import React, { useEffect, useState  } from 'react';
import {
  Container,
  Grid,
  makeStyles,
  Button,
  Box,
  Card,
  CardContent,
  CardHeader,
  Divider,
} from '@material-ui/core';
import Page from '../../componenets/Page';
import { employeePunchIn } from '../../services/api';
import { employeePunchOut } from '../../services/api';
import { getAttendence } from '../../services/api';


const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    paddingBottom: theme.spacing(3),
    paddingTop: theme.spacing(3)
  }
}));


const PunchInOut = () => {
  const classes = useStyles();
  const [punchedIn, setPunchedIn] = useState();
  let [attendenceArray, setAttendenceArray] = useState([]);
  let attendence = [];

  function createData(date, intime, outtime) {
    return { date, intime, outtime};
  }

  useEffect(() => {
    getAttendence().then((res) => {
        if(res.data.punch != undefined){
          const data = res.data.punch;
          for(let e of data) {
          attendence.push(createData(e.date, e.inTime, e.outTime))
          }
          setAttendenceArray(attendence);
          var lastpunch = attendence[attendence.length -1];
          if(lastpunch.outtime == undefined){
          setPunchedIn(true);
          }
          else{
          setPunchedIn(false);
          }        
        }
        else{
          setPunchedIn(false);
        }
      
    });
  }, [])

  const punchIn= (event) => {
    event.preventDefault();
    employeePunchIn().then((res) => {
      setPunchedIn(true);
    });
  }

  const punchOut= (event) => {
    event.preventDefault();
    employeePunchOut().then((res) => {
      setPunchedIn(false);
    });
  }
  
  
  var  renderCard = 
    <Card>      
      <CardHeader
        subheader="Record your IN time"
        title="Attendence"
      />
      <Divider />          
      <Box
        display="flex"
        justifyContent="flex-centre"
        p={2}
      >
        <Button
          color="primary"
          variant="contained"
          onClick = {punchIn}
        >
        Punch In
        </Button>
      </Box>
    </Card>
  
  if(punchedIn){
    renderCard = 
    <Card>      
    <CardHeader
      subheader="Record your Out time"
      title="Attendence"
    />
    <Divider />          
    <Box
      display="flex"
      justifyContent="flex-centre"
      p={2}
    >
      <Button
        color="primary"
        variant="contained"
        onClick = {punchOut}
      >
      Punch Out
      </Button>
    </Box>
  </Card>  
  }



  


  return (
    <Page
      className={classes.root}
      title="Attendence"
    >
      <Container maxWidth="lg">
        <Grid
          container
          spacing={3}
        >
          {/* <Grid
            item
            lg={4}
            md={6}
            xs={12}
          >
            <Profile />
          </Grid> */}
          <Grid
            item
            lg={12}
            md={12}
            xs={12}
          >
            
                
          {renderCard}
              
          </Grid>
        </Grid>
      </Container>
    </Page>
  );
};

export default PunchInOut;
