import React, { useEffect, useState  } from 'react';
import { Link as RouterLink, useLocation } from 'react-router-dom';
import PropTypes from 'prop-types';
import AttachMoneyIcon from '@material-ui/icons/AttachMoney';
import EventBusyIcon from '@material-ui/icons/EventBusy';
import HowToRegIcon from '@material-ui/icons/HowToReg';
import PersonAddOutlinedIcon from '@material-ui/icons/PersonAddOutlined';
import {
  Avatar,
  Box,
  Divider,
  Drawer,
  Hidden,
  List,
  Typography,
  makeStyles
} from '@material-ui/core';
import {
  BarChart as BarChartIcon,
  Users as UsersIcon,
  Edit2 as EditIcon,
  List as ListIcon,
  Book as SchedulingIcon,
} from 'react-feather';
import NavItem from './NavItem';
import {getEmployee} from '../../../services/api';


const user = {  
  avatar: '/static/images/avatars/avatar_1.jpg'
};

const managerItems = [
  {
    href: '/app/dashboard',
    icon: BarChartIcon,
    title: 'Dashboard'
  },
  {
    href: '/app/scheduling',
    icon: SchedulingIcon,
    title: 'Scheduling'
  },
  {
    href: '/app/approve/leave',
    icon: ListIcon,
    title: 'Leave Requests'
  },
  // {
  //   href: '/app/employees',
  //   icon: UsersIcon,
  //   title: 'Employees'
  // },
  {
    href: '/app/add-employee',
    icon: PersonAddOutlinedIcon,
    title: 'Add Employee'
  },
  {
    href: '/app/donation',
    icon: AttachMoneyIcon,
    title: 'Donation'
  },
  {
    href: '/app/get-employee',
    icon: EditIcon,
    title: 'Profile'
  },
];

const employeeItems = [
  {
    href: '/app/attendence',
    icon: HowToRegIcon,
    title: 'Attendence'
  },
  {
    href: '/app/leave',
    icon: EventBusyIcon,
    title: 'Leave'
  },
  {
    href: '/app/employee-schedule',
    icon: SchedulingIcon,
    title: 'Scheduling'
  },
  {
    href: '/app/get/leave',
    icon: ListIcon,
    title: 'All Leaves'
  },
  {
    href: '/app/donation',
    icon: AttachMoneyIcon,
    title: 'Donation'
  },
  {
    href: '/app/get-employee',
    icon: EditIcon,
    title: 'Profile'
  },

  // {
  //   href: '/login',
  //   icon: LockIcon,
  //   title: 'Login'
  // },
  // {
  //   href: '/register',
  //   icon: UserPlusIcon,
  //   title: 'Register'
  // },
  // {
  //   href: '/404',
  //   icon: AlertCircleIcon,
  //   title: 'Error'
  // }
];

const useStyles = makeStyles(() => ({
  mobileDrawer: {
    width: 256
  },
  desktopDrawer: {
    width: 256,
    top: 64,
    height: 'calc(100% - 64px)'
  },
  avatar: {
    cursor: 'pointer',
    width: 100,
    height: 100
  }
}));



const NavBar = ({ onMobileClose, openMobile }) => {
  const classes = useStyles();
  const location = useLocation();
  const [userName, setUsername] = useState();
  const [role, setRole] = useState();
  const [NavItems, setNavItems] = useState([]);
  //const userrole = localStorage.getItem("userrole")
  var nav;
  useEffect(() => {
    if (openMobile && onMobileClose) {
      onMobileClose();
    }
    getEmployee().then((res) => {
      setUsername(res.data.firstName +" "+ res.data.lastName);
      setRole(res.data.role);
      
        if(res.data.role == "Manager"){
          setNavItems(managerItems);
        }
        else{
          setNavItems(employeeItems);
        }
        
      console.log(NavItems);  
    })
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const content = (
    <Box
      height="100%"
      display="flex"
      flexDirection="column"
    >
      <Box
        alignItems="center"
        display="flex"
        flexDirection="column"
        p={2}
      >
        <Avatar
          className={classes.avatar}
          component={RouterLink}
          src={user.avatar}
          to="/app/account"
        />
        <Typography
          className={classes.name}
          color="textPrimary"
          variant="h5"
        >
          {userName}
        </Typography>
        <Typography
          color="textSecondary"
          variant="body2"
        >
          {role}
        </Typography>
      </Box>
      <Divider />
      <Box p={2}>
      <List>
        {
          NavItems.map((item) => (
          <NavItem
          href={item.href}
          key={item.title}
          title={item.title}
          icon={item.icon}
        />
      ))
        }
      </List>
      </Box>
      <Box flexGrow={1} />
      {/* <Box
        p={2}
        m={2}
        bgcolor="background.dark"
      >
        <Typography
          align="center"
          gutterBottom
          variant="h4"
        >
          Need more?
        </Typography>
        <Typography
          align="center"
          variant="body2"
        >
          Upgrade to PRO version and access 20 more screens
        </Typography>
        <Box
          display="flex"
          justifyContent="center"
          mt={2}
        >
          <Button
            color="primary"
            component="a"
            href="https://react-material-kit.devias.io"
            variant="contained"
          >
            See PRO version
          </Button>
        </Box>
      </Box> */}
    </Box>
  );

  return (
    <>
      <Hidden lgUp>
        <Drawer
          anchor="left"
          classes={{ paper: classes.mobileDrawer }}
          onClose={onMobileClose}
          open={openMobile}
          variant="temporary"
        >
          {content}
        </Drawer>
      </Hidden>
      <Hidden mdDown>
        <Drawer
          anchor="left"
          classes={{ paper: classes.desktopDrawer }}
          open
          variant="persistent"
        >
          {content}
        </Drawer>
      </Hidden>
    </>
  );
};

NavBar.propTypes = {
  onMobileClose: PropTypes.func,
  openMobile: PropTypes.bool
};

NavBar.defaultProps = {
  onMobileClose: () => {},
  openMobile: false
};

export default NavBar;
