import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import {
  makeStyles,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper
} from '@material-ui/core';
import { getLeaves } from '../../../services/api'

const useStyles = makeStyles(() => ({
  root: {},
  table: {
    minWidth: 650,
  },
}));

const LeaveList = ({ className, props }) => {
  const classes = useStyles();
  let [row, setRow] = useState([]);
  let rows = [];

  function createData(name, from, to, reason, status) {
    return { name, from, to, reason, status };
  }

  useEffect(() => {
    getLeaves().then((res) => {
      const data = res.data;
      for(let leave of data) {
        rows.push(createData(leave.username, leave.fromDate, leave.toDate, leave.reason, leave.status))
      }
      setRow(rows);
    });
  }, [])

  return (
    <TableContainer component={Paper}>
      <Table className={classes.table} aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell>User Name</TableCell>
            <TableCell >From</TableCell>
            <TableCell >To</TableCell>
            <TableCell >Reason</TableCell>
            <TableCell >Status</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {row.map((row) => (
            <TableRow key={row.name}>
              <TableCell component="th" scope="row">
                {row.name}
              </TableCell>
              <TableCell >{row.from}</TableCell>
              <TableCell >{row.to}</TableCell>
              <TableCell >{row.reason}</TableCell>
              <TableCell >{row.status}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
};

LeaveList.propTypes = {
  className: PropTypes.string
};

export default LeaveList;
