import React from 'react';
import { Navigate } from 'react-router-dom';
import DashboardLayout from './layouts/DashboardLayout';
import MainLayout from './layouts/MainLayout';
// import AccountView from 'src/views/account/AccountView';
import EmployeeListView from './views/employee/EmployeeListView';
import AddEmployee from './views/employee/AddEmployee';
import DashboardView from './views/DashboardView';
import ScheduleListView from './views/schedule/ScheduleListView';
import ScheduleEmployeeView from './views/schedule/ScheduleEmployeeView';
import LoginView from './views/Authentication/LoginView';
// import NotFoundView from 'src/views/errors/NotFoundView';
// import ProductListView from 'src/views/product/ProductListView';
import RegisterView from './views/Authentication/RegisterView';
import VerifyAccountView from './views/Authentication/VerifyAccountView';
import OrganizationDetailsVIew from './views/Authentication/OrganizationDetailsVIew';
import ForgetPasswordView from './views/Authentication/ForgetPasswordView';
import VerifyForgetPasswordView from './views/Authentication/VerifyForgetPasswordView';
import ChangePasswordView from './views/Authentication/ChangePasswordView';
import UpdateEmployee from './views/employee/UpdateEmployee';
import LandingPage from './views/LandingPage/LandingPage';
import Attendence from './views/Attendence/index';
import Leave from './views/employee/Leave';
import Leaves from './views/employee/GetLeaves';
import Donation from './views/Donation/index';
import DonationSuccess from './views/Donation/donationSuccess';
import ApproveLeave from './views/employee/ApproveLeave';
// import SettingsView from 'src/views/settings/SettingsView';

const routes = [
  {
    path: 'app',
    element: <DashboardLayout />,
    children: [
      // { path: 'account', element: <AccountView /> },
      { path: 'employees', element: <EmployeeListView /> },
      { path: 'add-employee', element: <AddEmployee /> },
      { path: 'dashboard', element: <DashboardView /> },
      { path: 'scheduling', element: <ScheduleListView /> },
      { path: 'employee-schedule', element: <ScheduleEmployeeView /> },
      { path: 'get-employee', element: <UpdateEmployee /> },
      { path: 'attendence', element: < Attendence/> },
      { path: 'donation', element: <Donation/>},
      { path: 'leave', element: <Leave /> },
      { path: 'get/leave', element: <Leaves /> },
      { path: 'approve/leave', element: <ApproveLeave /> },
      // { path: 'products', element: <ProductListView /> },
      // { path: 'settings', element: <SettingsView /> },
      // { path: '*', element: <Navigate to="/404" /> }
    ]
  },
  {
    path: '/',
    element: <MainLayout />,
    children: [
      { path: 'login', element: <LoginView /> },
      { path: 'register', element: <RegisterView /> },
      { path: 'verify', element: <VerifyAccountView /> },
      { path: 'details', element: <OrganizationDetailsVIew /> },
      { path: 'forget-password', element: <ForgetPasswordView /> },
      { path: 'verify-code', element: <VerifyForgetPasswordView /> },
      { path: 'change-password', element: <ChangePasswordView /> },
      { path: 'success', element: <DonationSuccess/>},
      { path: 'landingpage', element: <LandingPage /> },
      // { path: '404', element: <NotFoundView /> },
      { path: '/', element: <Navigate to="/landingpage" /> },
      // { path: '*', element: <Navigate to="/404" /> }
    ]
  }
];

export default routes;
