import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';


const useStyles = makeStyles((theme) => ({
  container: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  textField: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
    width: 200,
  },
}));


export default function ScheduleDilougForm({id, name, startdate, date, starttime, endtime, day, onAdd, onDelete, ...rest}) {

  const classes = useStyles();

    const [open, setOpen] = useState(false);
    const [start, setstart] = useState(starttime);
    const [end, setend] = useState(endtime);
  
    const handleClickOpen = () => {
      setOpen(true);
    };
  
    const handleClose = () => {
      setOpen(false);
    };

    const sendData = () => {
      if(start && end)
        onAdd({id, startdate, day, date, start, end})
      else
        alert("Entries cannot be null");
    };

    const deleteData = () => {
      if(start && end)
      {
        onDelete({id, startdate, day, date, start, end});
        setstart();
        setend();
      }
      else
        alert("Null entries cannot be deleted");
    };

    const handleStartTime = (event) => {
      // if(!validateFirstName(event.target.value)) {
      //   setOpen(true);
      //   setMessage("Please Enter Valid First Name")
      // } else {
      //   setOpen(false);
      //   setFirstname(event.target.value)
      // }
      setstart(event.target.value)
    }

    const handleEndTime = (event) => {
      // if(!validateFirstName(event.target.value)) {
      //   setOpen(true);
      //   setMessage("Please Enter Valid First Name")
      // } else {
      //   setOpen(false);
      //   setFirstname(event.target.value)
      // }
      setend(event.target.value)
    }

  return (
    <div>
      <Button variant="outlined" color="primary" onClick={handleClickOpen}>
        {start} - {end}
      </Button>
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title">{name}</DialogTitle>
        <DialogContent>
          <DialogContentText>
            {Date(date).slice(0,15)}
          </DialogContentText>
          <form
            className={classes.container}
            noValidate
            // onSubmit={onSubmitForm}
            autoComplete="off"
          >
            <TextField
            autoFocus
            margin="dense"
            id="start"
            label="Start Time"
            type="time"
            defaultValue={starttime}
            className={classes.textField}
            onChange={event => handleStartTime(event)}
            />
            <TextField
            margin="dense"
            id="end"
            label="End Time"
            type="time"
            defaultValue={endtime}
            className={classes.textField}
            onChange={event => handleEndTime(event)}
            />
          </form>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Cancel
          </Button>
          <Button onClick={() => {deleteData(); handleClose()}} color="primary">
            Delete
          </Button>
          <Button onClick={() => {sendData(); handleClose()}} color="primary">
            Save
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
