import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import {
  makeStyles,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper
} from '@material-ui/core';
import DeleteOutlineIcon from '@material-ui/icons/DeleteOutline';
import { getOrganizationEmployees } from '../../../services/api'

const useStyles = makeStyles(() => ({
  root: {},
  table: {
    minWidth: 650,
  },
}));

const EmployeeList = ({ className, props }) => {
  const classes = useStyles();
  let [employeeArray, setEmployeeArray] = useState([]);
  let employees = [];

  function createData(firstname, lastname, username, role) {
    return { firstname, lastname, username, role };
  }

  function deleteEmployee(username) {
    
  }

  useEffect(() => {
    getOrganizationEmployees().then((res) => {
      const data = res.data;
      for(let e of data) {
        employees.push(createData(e.firstName, e.lastName, e.username, e.role))
      }
      setEmployeeArray(employees);
    });
  }, [])

  return (
    <TableContainer component={Paper}>
      <Table className={classes.table} aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell>First Name</TableCell>
            <TableCell >Last Name</TableCell>
            <TableCell >Email</TableCell>
            <TableCell >Role</TableCell>
            {/* <TableCell >Action</TableCell> */}
          </TableRow>
        </TableHead>
        <TableBody>
          {employeeArray.map((row) => (
            <TableRow key={row.firstname}>
              <TableCell component="th" scope="row">
                {row.firstname}
              </TableCell>
              <TableCell >{row.lastname}</TableCell>
              <TableCell >{row.username}</TableCell>
              <TableCell >{row.role}</TableCell>
              {/* <TableCell ><DeleteOutlineIcon style={{cursor: "pointer"}} onClick={deleteEmployee(row.username)}/></TableCell> */}
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
};

EmployeeList.propTypes = {
  className: PropTypes.string
};

export default EmployeeList;
