import React, { useEffect, useState } from 'react';
import {
  Box,
  Container,
  makeStyles
} from '@material-ui/core';
import Page from '../../../componenets/Page';
import Results from './Results';
import Toolbar from './Toolbar';
import { getOrganizationEmployees } from '../../../services/api';

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    paddingBottom: theme.spacing(3),
    paddingTop: theme.spacing(3)
  }
}));


const ScheduleListView = () => {
  const classes = useStyles();
  const [employees, setEmployee] = useState([]);
  let employeeArray =[];
  
  function createData(id, username, firstName, lastName, week) {
    return { id, username, firstName, lastName, week };
  }

  function createDay(starttime, endtime){
    return {starttime, endtime}
  }
  
  useEffect(() => {
    getOrganizationEmployees().then((res) => {
      const data = res.data;
      console.log(data);
      for(let employee of data) {
        let week = [createDay('',''),createDay('',''),createDay('',''),createDay('',''),createDay('',''),createDay('',''),createDay('','')];
        if(employee.schedule !== undefined){
          for(let day of employee.schedule.entries) {
            // console.log("Day ",day);
            switch(day.day){
              case 'monday':
                week[0] = createDay(day.starttime, day.endtime);
                break;
              case 'tuesday':
                week[1] = createDay(day.starttime, day.endtime);
                break;
              case 'wednesday':
                week[2] = createDay(day.starttime, day.endtime);
                  break;
              case 'thursday':
                week[3] = createDay(day.starttime, day.endtime);
                break;
              case 'friday':
                week[4] = createDay(day.starttime, day.endtime);
                break;
              case 'saturday':
                week[5] = createDay(day.starttime, day.endtime);
                break;
              case 'sunday':
                week[6] = createDay(day.starttime, day.endtime);
                  break;                
              default:
            }
  
          }
        }
        
        employeeArray.push(createData(employee._id, employee.username, employee.firstName, employee.lastName, week))
      }
      setEmployee(employeeArray);
      // console.log(employees);
    })
    
  }, [])

  const today = new Date();
  const [date, setDate] = useState(today.setDate(today.getDate() + (7-today.getDay())%7+1));

  return (
    <Page
      className={classes.root}
      title="Schedule"
    >
      <Container maxWidth={false}>
        <Toolbar date={date} onNextClick={(value) => setDate(value)} onPrevClick={(value) => setDate(value)} />
        <Box mt={3}>
          <Results date={date} employees={employees} />
        </Box>
      </Container>
    </Page>
  );
};

export default ScheduleListView;
