export const employeePunchIn = async (data) => {
    console.log("Call attendence API");
    const token = localStorage.getItem('token')
    const organizationId = localStorage.getItem('organizationId');
    const userId = localStorage.getItem('userId')
    const response = await fetch('/attendence/in', {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
            "Authorization": "Bearer "+ token
        },
        body: JSON.stringify({
            userid: userId,
            organizationid: organizationId
        })
    }).then((res) => res.json())
    .catch((err) => {
        console.log(err)
        return err;
    });

    if(response.status === "ok") {
        var punchArray = response.data.punch;
        var lastPunch = punchArray[punchArray.length - 1].date;
        localStorage.setItem('punchedIn', lastPunch);
        return response
        
    } else {
        return response
    }
}

export const employeePunchOut = async (data) => {
    console.log("Call attendence API");
    const token = localStorage.getItem('token');
    const organizationId = localStorage.getItem('organizationId');
    const userId = localStorage.getItem('userId')
    const response = await fetch('/attendence/out/', {
        method: 'PUT',
        headers: {
            'Content-Type': 'application/json',
            "Authorization": "Bearer "+ token
        },
        body: JSON.stringify({
            userid: userId,
            organizationid: organizationId
        })
    }).then((res) => res.json())
    .catch((err) => {
        console.log(err)
        return err;
    });

    if(response.status === "ok") {
        localStorage.removeItem('punchedIn');
        return response
    } else {
        return response
    }
}

